import React, {useState, useEffect} from "react";
import './style.scss';

export default function Modal ({children, className = '', visible, onCancel, showClose = true}) {
    let [show, setShow] = useState(false);
    let [isIn, setIsIn] = useState(true);
    const handleCancel = () => {
        onCancel && onCancel();
    };

    useEffect(() => {
        if(visible) {
            if(!show) {
                setShow(true);
                setIsIn(true);
            }
        } else {
            if(show) {
                setTimeout(() => {
                    setShow(false);
                }, 220);
                setIsIn(false);
            }
        }
    }, [visible]);

    return show ? (
        <div className={`c-modal-mask ${isIn ? 'in' : 'out'}`}>
            <div className={`c-modal-content ${isIn ? 'in' : 'out'}`}>
                <div className={`c-modal-body ${className}`}>
                    {children}
                </div>
                {showClose && <div className='c-modal-footer' onClick={handleCancel} />}
            </div>
        </div>
    ) : null
}
