import React from "react";
import './style.scss';
import { useTranslation } from 'react-i18next';

function LanguageSwitch ({className = ''}) {
    const { i18n } = useTranslation();
    let language = i18n.language;

    async function changeLanguage  (lan)  {
        await i18n.changeLanguage(lan);
        window.localStorage['white_crow_language'] = lan;
    }

    return (
        <div className={`c-languageSwitch ${className}`}>
            <div onClick={async  () => {
                await changeLanguage('en');
            }} className={`${language === 'en' ? 'active' : ''}`}>English</div>
            <div onClick={async () => {
                await changeLanguage('cn');
            }} className={`${language === 'cn' ? 'active' : ''}`}>中文</div>
        </div>
    );
}

export default LanguageSwitch;
