import React, { useEffect } from "react";
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import "./plugin/star.js";
import "demos";
import { GlobalContext } from "./components";
import Router from "./Router";
import { toast } from "./utils";
import "./css/global.scss";
// import {getBalance, getUTXO} from "./service";
import useSetState from "./hooks/useSetState";
import VConsole from "vconsole";
import resources from "./local";

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources: resources,
    lng: "en",
    fallbackLng: "en",
    debug: process.env.NODE_ENV !== "production",
    interpolation: {
      escapeValue: false,
    },
  });

function App() {
  const { t } = useTranslation();
  const [state, setState] = useSetState({
    address: "",
  });

  const updateStore = (newState) => {
    setState(newState);
  };

  useEffect(() => {
    // function addr() {
    //     return new Promise(function (resolve) {
    //         setTimeout(function () {
    //             // resolve({address: 'TcS8a787PfU8b754FeqWHbQFSykGUDpY4Uu'})
    //             resolve({address: 'TcASfmyoFiNobNaCHeiPMkWgGmovoxk1sqk', currency: 'DOS'})
    //         }, 100)
    //     })
    // }
    async function getAddress() {
      // let {address, currency} = process.env.NODE_ENV === 'production'
      //     ? await window.demos.getIdentity()
      //     // : await window.demos.getIdentity();
      // : await addr();
      let { address, currency } = await window.demos.getIdentity();

      if (currency === "DOS") {
        updateStore({ address });
        // reloadData(address);
      } else {
        toast("请切换到DOS钱包再试", 600000);
      }
    }

    if (process.env.NODE_ENV === "development") {
      new VConsole();
    }

    let timer = setTimeout(() => {
      toast(t("address_hint"), 300000);
    }, 2000);
    getAddress().then(() => {
      clearTimeout(timer);
    });

    if (window.localStorage["white_crow_language"] === "cn") {
      i18n.changeLanguage("cn");
    }
    // i18n.changeLanguage('cn');
  }, []);

  const reloadData = (address) => {
    // getAddressBalance(address);
    // getAddressUTXO(address);
  };

  // console.log();
  return (
    <GlobalContext.Provider
      value={{ ...state, updateStore, reloadData: reloadData, t: t }}
    >
      {/*{state.loadUtxo && <MaskLoading />}*/}
      <Router />
    </GlobalContext.Provider>
  );
}

export default App;
