import React from "react";
import "./style.scss";

const Nav = ({ title, history }) => {
  const goBack = () => {
    history.goBack();
  };
  return (
    <div className="basic_layout">
      <div className="basic_layout-header">
        <div className="leading" onClick={goBack}>
          <img src={`${process.env.PUBLIC_URL}/images/arrow_left.png`} alt="" />
        </div>
        <div className="title">{title}</div>
        <div className="action" />
      </div>
    </div>
  );
};

export default Nav;
