function getRecords(address) {
    let records = localStorage[`rechargeReturn_${address}`];
    let recordsJson = {};
    if (records) {
        try {
            recordsJson = JSON.parse(records);
        } catch (e) {}
    }

    return recordsJson
}

export default {
    get: function (address, id) {
        let recordsJson = getRecords(address);
        return recordsJson[id] || '';
    },
    set: function (address, id, tx) {
        let recordsJson = getRecords(address);
        recordsJson[id] = tx;
        localStorage[`rechargeReturn_${address}`] = JSON.stringify(recordsJson);
    },
    remove: function (address, id) {
        let recordsJson = getRecords(address);
        delete recordsJson[id];
        localStorage[`rechargeReturn_${address}`] = JSON.stringify(recordsJson);
    }
}
