import i18n from 'i18next';
import toast from "./toast";

const errors = {
  1001: 'error_1001', // 未发现用户
  1002: 'error_1002', // 用户被禁用
  1003: 'error_1003', // 用户未激活
  1004: 'error_1004', // 数据操作异常
  1005: 'error_1005', // 参数错误
  1006: 'error_1006', // 邀请码无效
  1007: 'error_1007', // 广播失败
  1008: 'error_1008', // 交易错误
  1009: 'error_1009', // 用户已激活
};

function showError(code) {
  let err = errors[code] || 'error_0';
  toast(i18n.getResource(i18n.language, 'translation', err));
}

export default showError;
